
import Event from './Event.vue';
import EventInput from './EventInput.vue';
import { defineComponent, ref } from 'vue';
export default defineComponent({
  name: 'App',
  components: {
    Event,
    EventInput,
  },
  setup() {
    const eventList = ref([
      {
        id: 0,
        date: '2022-02-15',
        title: 'Year Renovation Programm 2022',
        place: 'New York',
        status: false,
      },
      {
        id: 1,
        date: '2021-11-15',
        title: 'Year Renovation Programm 2021',
        place: 'New York',
        status: false,
      },
    ]);
    const addEvent = ({
      date,
      title,
      place,
    }: {
      date: string;
      title: string;
      place: string;
    }) => {
      eventList.value = [
        ...eventList.value,
        {
          id: eventList.value[eventList.value.length - 1].id + 1,
          date,
          title,
          place,
          status: false,
        },
      ];
    };
    const setDoneEvent = (id: number) => {
      eventList.value = eventList.value.map((x) => {
        if (x.id === id) x.status = true;
        return x;
      });
    };
    const removeEvent = (id: number) => {
      eventList.value = eventList.value.filter((x) => x.id !== id);
    };
    return {
      eventList,
      addEvent,
      removeEvent,
      setDoneEvent,
    };
  },
});
