
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'AppEvent',
  components: {},
  emits: ['onDone', 'onRemove'],
  props: {
    model: {
      requi$red: true,
      default: {
        id: 0,
        date: '2022-02-15',
        title: 'Year Renovation Programm 2021',
        place: 'New York',
        status: false,
      },
    },
  },
  setup(props, { emit }) {
    const emitOnRemove = () => {
      emit('onRemove');
    };
    const emitOnDone = () => {
      emit('onDone');
    };

    return {
      emitOnRemove,
      emitOnDone,
    };
  },
});
