
import { defineComponent, ref } from 'vue';
export default defineComponent({
  emits: {
    onAddEvent({ date, title, place }) {
      if (date === '' || title === '' || place === '') {
        alert('Fill some info please!');
        return false;
      }
      return true;
    },
  },
  setup(props, { emit }) {
    const date = ref('');
    const title = ref('');
    const place = ref('');
    const onAddEvent = () => {
      emit('onAddEvent', {
        date: date.value,
        title: title.value,
        place: place.value,
      });
    };
    return {
      date,
      title,
      place,
      onAddEvent,
    };
  },
});
